import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Category, Database } from '../../providers/database';
/**
 * Generated class for the HotOffer page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-hotoffers',
  templateUrl: 'hotoffers.html',
})
export class HotoffersPage { 
  //this is a variable shows that this section is active || selected 
  public ActiveNow : number = -1; 

  //A flag to show that the data is ready loaded or not 
  public ReadyData : boolean = false;  

  //A variable to store the data comes from the service in it 
  public serviced_data : any;

  menuItems: Category[];
  constructor(public storage : Storage , public navCtrl: NavController, public navParams: NavParams) {
    let db = Database.getInstance();
    this.menuItems = db.parentCategory();  
      
    //getting the 'data' with orange color from the service 
    // this data comes from app.component.ts line 222
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HotoffersPage');
  } 
  
  categories(id: string) {
    this.menuItems.forEach(item => {
      if(item.id === id) {
        this.navCtrl.push('CategoriesPage', {menus: item, select: item.children[0].name.toLowerCase()});
      }
    })
  }
}



